import React, { useState, useEffect } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import { Card, Table, Input, Select, Button, Popconfirm, message } from 'antd'
import { reqProducts, reqDelProducts, reqSearchProductsName, reqSearchProductsDesc, reqUpdateState } from '../../api/index'
import { PAGE_SIZE } from '../../utils/constant'
import { PullRequestOutlined } from '@ant-design/icons'
const Option = Select.Option
/**
 * 
 * 
 * 
 *     1).
 *          
 *         
 *             pageNumpageSize
 *     2).
 *         
 *         
 *             pageNumpageSize
 *             +total
 *     3).
 *         
 *         
 */
function Home(props) {

    const history = useHistory();
    const [productData, setProductData] = useState([]) // 
    const [currentNum, setCurrentNum] = useState(1)
    const [total, setTotal] = useState(0)   //
    const [loading, setLoading] = useState(false)    //
    const [searchName, setSearchName] = useState('') //input
    const [searchType, setSearchType] = useState('0') //



    useEffect(() => {
        //
        //  
        if (props.location.params) {
            setCurrentNum(props.location.params.pageNum)
            loadData(props.location.params.pageNum)
        } else {
            //api (pageNum)(pageSize)
            loadData(1)
        }


    }, [])

    /**
     * 
     * pageNum->
     */
    const loadData = async (pageNum) => {
        //  
        if (searchName) {
            setLoading(true) //loading
            searchProduct(pageNum, searchName, searchType)
            setLoading(false) //loading
        } else {
            setLoading(true) //loading
            const result = await reqProducts(pageNum, PAGE_SIZE)
            setLoading(false) //loading
            if (result.data.status === 0) {
                console.log(result);
                const { total, list } = result.data.data  //()
                setProductData(list)
                setTotal(total)
            }
        }
    }
    /**
     * 
     * pageNum->
     * searchName->
     * searchType->
     */
    const searchProduct = async (pageNum, searchName, searchType) => {
        if (searchType === '0') {
            const result = await reqSearchProductsName(pageNum, PAGE_SIZE, searchName)
            if (result.data.status === 0) {
                //
                const { list, total } = result.data.data
                setProductData(list)
                setTotal(total)
            }

        } else if (searchType === '1') {
            const result1 = await reqSearchProductsDesc(pageNum, PAGE_SIZE, searchName)
            if (result1.data.status === 0) {
                //
                const { list, total } = result1.data.data
                setProductData(list)
                setTotal(total)
            }
        }
    }
    /**
     * 
     */

    const columns = [
        {
            title: '',
            dataIndex: 'name',
            width: '10%'
        },
        {
            title: '',
            dataIndex: 'imgs',
            width: '20%',
            render: (txt, record, index) => {
                return (
                    <img style={{ width: '200px' }} src={`/upload/${record.imgs}`} alt="" />
                )
            }
        },
        {
            title: '',
            dataIndex: 'desc',
            width: '25%'
        },
        {
            title: "",
            dataIndex: 'price',
            width: '5%',
            render: (price) => { return '' + price }
        },
        {
            title: '',
            dataIndex: 'status',
            width: '8%',
            render: (status) => {
                return status === 1 ? "" : ""
            }
        },
        {
            title: '',
            width: '27%',
            render: (txt, record, index) => {
                return (
                    <div>
                        <Button type="primary" style={{ margin: "0 8px" }} onClick={() => history.push({
                            // id 
                            pathname: `/products:/product/edit/${record._id}`, params: { pageNum: currentNum }
                        })}></Button>
                        <Button style={{ margin: "0 8px" }} onClick={() => {
                            reqUpdateState(record._id, record.status === 1 ? 2 : 1)
                                .then(res => {
                                    message.success("")
                                    loadData(currentNum)
                                })
                                .catch(err => {
                                    message.error("")
                                })
                        }}>{record.status === 1 ? "" : ""}</Button>
                        <Popconfirm
                            title=""
                            onCancel={() => console.log('' + record)}
                            onConfirm={() => {
                                //API
                                console.log(record._id);
                                reqDelProducts(record._id)
                                    .then(res => {
                                        //
                                        message.success('')
                                        loadData(currentNum)
                                    })
                                    .catch(err => message.error(err))
                            }}>
                            <Button type="danger" style={{ margin: "0 8px" }}></Button>
                        </Popconfirm>
                        <Button type="primary" style={{ margin: "0 8px" }} onClick={() => history.push('/products:/product/detail')}></Button>
                    </div>
                )
            }
        }


    ]
    //Cardtitle 
    const title = (
        <div style={{ width: '200px' }}>
          
            <Select defaultValue='0' style={{ width: '120px' }} onChange={(p) => setSearchType(p)}>
                <Option value='0'></Option>
                <Option value='1'></Option>
            </Select>
            <Input placeholder='' style={{ margin: '0 10px' }} onChange={(event) => setSearchName(event.target.value)}></Input>
            <Button type="primary" onClick={() => {
                //
                loadData(currentNum)
            }}></Button>

        </div>
    )
    //Cardextra 
    const extra = (
        <Button type='primary' onClick={() => history.push({
            pathname: '/products:/product/edit',
            params: { pageNum: currentNum }
        })}>+ </Button>
    )
    return (
        <Card title={title} extra={extra}>
            <Table

                columns={columns}
                dataSource={productData}
                rowKey="_id"
                loading={loading}
                pagination={{
                    total: total,
                    current: currentNum,
                    defaultPageSize: PAGE_SIZE,
                    onChange: (pageNum) => {
                        setCurrentNum(pageNum)
                        loadData(pageNum)
                    }
                }}
                bordered>

            </Table>
        </Card>
    )
}

export default Home
