import React, { useState, useEffect } from 'react'
import './index.less'
import menuList from '../../config/menuConfig'
import { Menu, Dropdown, Modal, Button, Space } from 'antd';
import { DownOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import { formateDate } from '../../utils/dateUtils'
import { withRouter, useHistory } from 'react-router-dom';
//user
import memoryUtils from '../../utils/memoryUtils'
import { getUser,removeUser } from '../../utils/storageUtils'
import currentUser from '../../utils/memoryUtils'

const { confirm } = Modal;
function Header(props) {
    const [currentTime, setCurrentTime] = useState(''); //
    const [currentTitle, setCurrentTitle] = useState(''); //
    // const [currentUser,setCurrentUser] = useState('')
    const history = useHistory();
    let intId = ''
    useEffect(() => {
        
        
        setCurrentTime(formateDate(Date.now())) //
        getTime() //
        //returncomponentWillUnmount
        return ()=>{
            clearInterval(intId)
        }   
    }, [])
    //useEffectcomponentDidMount

    useEffect(() => {
        menuList.forEach(item => {
            if (item.key === props.location.pathname) {
                setCurrentTitle(item.title)
            } else if (item.children) {
                item.children.map(p => {
                    if (p.key === props.location.pathname) {
                        setCurrentTitle(p.title)
                    }
                })
            }
        })
    },[props.location.pathname])//
    //componentDidMountcomponentDidUpdate
    
   
    const showConfirm = () => {
        confirm({
            title: '',
            icon: <ExclamationCircleOutlined />,
            onOk() {
                //
                console.log('OK');
                //user
                removeUser()
                memoryUtils.user={}
                //login
                history.replace('/login')
               
                

            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }
    const getTime = () => {
        intId = setInterval(() => {
            setCurrentTime(formateDate(Date.now()))
        }, 1000)
        
    }
    const menu = (
        <Menu>
            <Menu.Item>
                <Button></Button>
            </Menu.Item>
            <Menu.Item>
                <Button onClick={showConfirm} style={{ width: "100%" }}></Button>
            </Menu.Item>


        </Menu>
    );


    return (
        <div className="header">
            <div className="header-top">

                <Dropdown overlay={menu}>
                    <a className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                        {currentUser.user.username} <DownOutlined />
                    </a>
                </Dropdown>
            </div>
            <div className="header-bottom">
                <div className="header-bottom-left">{currentTitle}</div>
                <div className="header-bottom-right">
                    <span>{currentTime}</span>
                    <img src="http://api.map.baidu.com/images/weather/day/qing.png" alt="" />
                    <span></span>
                </div>
            </div>
        </div>
    )
}

export default withRouter(Header) 
