/**
 * 
 */
import React, { useState, useEffect } from 'react'
import { Card, Button, Table, Modal, Form, Input, message, Tree } from 'antd'
import { PAGE_SIZE } from '../../utils/constant'
import { reqRoles, reqAddRoles,reqUpdateRoles,reqDelRoles } from '../../api'
import menuList from '../../config/menuConfig'
import {formateDate} from '../../utils/dateUtils'

function Role() {
    const [dataSource, setDataSource] = useState([])//table
    const [role, setRole] = useState([]) //role
    const [buttonShow, setButtonShow] = useState(false) //
    const [loading, setLoading] = useState(false)//table
    const [isShow, setIsShow] = useState(false) //
    const [isShowAuth, setIsShowAuth] = useState(false) //
    const [checked,setChecked] = useState([]) //url
    const [form] = Form.useForm()  //form
    useEffect(() => {
        //
        loadData()
        //
    }, [])
    // const rowSelection = {}
    const loadData = async () => {
        setLoading(true)
        const result = await reqRoles()
        // console.log(result);
        if (result.data.status === 0) {
            setDataSource(result.data.data)
            setLoading(false)
        } else {
            message.error('')
            
        }
    }
    //
    const columns = [
        {
            title: '',
            dataIndex: 'name'
        },
        {
            title: '',
            dataIndex: 'create_time',
            render:(create_time)=>formateDate(create_time)
            
        },
        {
            title: '',
            dataIndex: 'auth_time',
            render:formateDate
        },
        {
            title: '',
            dataIndex: 'auth_name'
        },
        {
            title:'',
            render:(record)=>{
            return <Button type="danger" onClick={()=>delRole(record._id)}></Button>
            }
        }
    ]
    //
    const treeData = [
        {
            title: '',
            key: '0-0',
            children: [
                {
                    title: '',
                    key: '/home',
                    
                },
                {
                    title: '',
                    key: '/products:',
                    children: [
                        {
                            title: (
                                <span
                                    style={{
                                        color: '#1890ff',
                                    }}
                                >
                                    
                                </span>
                            ),
                            key: '/products:/category',
                        },
                        {
                            title: '',
                            key: '/products:/product',
                        },
                    ],
                },
                {
                    title: '',
                    key: '/user'
                },
                {
                    title: '',
                    key: '/role'
                },
                {
                    title: '',
                    key: '/charts:',
                    children: [
                        {
                            title: '',
                            key: '/charts:/bar'
                        },
                        {
                            title: '',
                            key: '/charts:/line'
                        },
                        {
                            title: '',
                            key: '/charts:/pie'
                        }
                    ]
                },
            ],
        },
    ];
    const onSelect = (selectedKeys, info) => {
        console.log('selected', selectedKeys, info);
    };

    const onCheck = (checkedKeys, info) => {
        setChecked(checkedKeys)
        
        console.log('onCheck', checkedKeys, info);
    };
    const delRole = async (role_id)=>{
        console.log(role_id);
        const result = await reqDelRoles(role_id)
        if(result.data.status===0){
            message.success("")
            loadData()
        }
    }
    const addRoleHandleOk = async () => {
        setIsShow(false)//model
        try {
            const values = await form.validateFields();
            console.log('success', values)
            // 
            const result = await reqAddRoles(values.roleName)

            if (result.data.status === 0) {
                loadData()
                form.resetFields()
            }
        } catch (errorInfo) {
            console.log('err' + errorInfo);
        }
    }
    //ok
    const settingHandleOk = () => {
        setIsShowAuth(false)    //
        setRole(role,...role.menus=checked) // role 
        console.log(checked);
        console.log(role);
        //api
        reqUpdateRoles(role).then(res=>{
            console.log(res);
            if(res.data.status===0){
                message.success('')
            }
        
        }).catch(err=>{
            console.log(err)
        })
        loadData()


    }
    //
    const onRow = (record) => {
        return {
            onClick: event => {
                //
                console.log(record);
                //
                setRole(record)
                //
                setButtonShow(true)
            }
        }
    }
    //Cardtitle
    const title = (
        <div>
            <Button
                type="primary"
                style={{ margin: "0 10px" }}
                onClick={() => setIsShow(true)}>
                
                </Button>
            <Button
                type="primary"
                disabled={!buttonShow}
                onClick={() => setIsShowAuth(true)}>
                
                </Button>
        </div>
    )

    return (
        <Card title={title}>
            <Table
                bordered
                rowKey={record => record._id}
                loading={loading}
                dataSource={dataSource}
                columns={columns}
                pagination={{ defaultPageSize: PAGE_SIZE }}
                rowSelection={{ type: 'radio', selectedRowKeys: [role._id] }} //
                onRow={onRow}
            >

            </Table>
            <Modal
                title=""
                visible={isShow}
                destroyOnClose={() => { return true }}
                onOk={addRoleHandleOk}
                onCancel={() => {
                    setIsShow(false)
                    form.resetFields()
                }}
                style={{ textAlign: "center" }}
            >
                <Form

                    name="basic"
                    form={form}
                >
                    <Form.Item
                        label=""
                        name="roleName"
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                        ]}
                    >
                        <Input />
                    </Form.Item>

                </Form>
            </Modal>
            <Modal
                title=""
                visible={isShowAuth}
                //modal
                destroyOnClose={() => { return true }}
                onOk={settingHandleOk}
                onCancel={() => {
                    setIsShowAuth(false)

                }}
                style={{ textAlign: "center" }}
            >
                <Form
                    name="auth"
                    form={form}
                >
                    <Form.Item label="">
                        <Input value={role.name} disabled></Input>
                    </Form.Item>
                </Form>
                <Tree
                    checkable
                    draggable='true'
                    //
                    defaultExpandAll='true'
                    
                    // checkedKeys={role.menu}
                 
                    defaultSelectedKeys={role.menus}
                    defaultCheckedKeys={role.menus}
                    onSelect={onSelect}
                    onCheck={onCheck}
                    treeData={treeData}
                />
            </Modal>
        </Card>
    )
}

export default Role
