import React, { useState, useEffect } from 'react'
import { Card, Form, Button, Input, Select, Cascader, message, Upload, Modal, Spin } from 'antd'
import { ArrowLeftOutlined, PlusOutlined } from '@ant-design/icons'
import { reqCategorys, reqFindOneProduct } from '../../api/index'
import PicturesWall from './PicturesWall'
import { reqAddProducts, reqDelImg, reqUpdateProduct } from '../../api/index'
import { useHistory, useParams } from 'react-router-dom'

const Option = Select.Option
const { TextArea } = Input;

const formItemLayout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 8 },
}
const formTailLayout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 8, offset: 4 },
};
function getBase64(file) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
    });
}

/**
 * 
 */
function Edit(props) {
    const params = useParams();
    const history = useHistory()
    const [form] = Form.useForm();
    const [options, setOptions] = useState([]) //
    const [imgUrl, setImgUrl] = useState('') //
    const [spinning, setSpinning] = useState(false)
    const [paramsId, setParamsId] = useState('');
    //3.state
    const [previewVisible, setPreviewVisible] = useState(false); //
    const [previewImage, setPreviewImage] = useState(''); //url
    const [previewTitle, setPreviewTitle] = useState('');
    const [fileList, setFileList] = useState([]); //
    //3.

    // const [defaultFileList,setDefaultFileList] = useState([])
    useEffect(() => {
        //params.id
        if (params.id) {
            //
            setSpinning(true)
            reqFindOneProduct(params.id)
                .then(res => {
                    console.log(res);
                    if (res.data.status === 0) {
                        const result = res.data.data
                        //error setProductID ID
                        //useState id idparams.id redux
                        // console.log(result.imgs);
                        setImgUrl(result.imgs[0])
                        setParamsId(params.id)
                        params.id = result.pCategoryId
                        getCategorys('0') //
                        // 
                        setFileList([{
                            uid: '-1',
                            name: result.imgs[0],
                            status: 'done',
                            url: `/upload/${result.imgs[0]}`
                        }])
                        //
                        form.setFieldsValue({
                            name: result.name,
                            price: result.price,
                            desc: result.desc,
                            category: [result.pCategoryId, result.categoryId]
                        })
                    }
                })
        } else {
            getCategorys('0')
        }

    }, [])

    //Cardtitle
    const title = (
       
        <div >
            <ArrowLeftOutlined onClick={() => history.push({ 
                 // 
                pathname: '/products:/product', 
                params: { pageNum: props.location.params.pageNum } 
                })} />
        </div>
    )
    //
    const validatorPrice = (rule, value, callback) => {
        if (value * 1 > 0) {
            //
            callback()
        } else {
            //
            callback('0')
        }
    }

    ///  
    //async promisepromiseasync
    const getCategorys = async (parentId) => {
        const result = await reqCategorys(parentId) //{status:0,data:categorys}
        if (result.data.status === 0) {
            const categorys = result.data.data

            if (parentId === '0') {
                // option() 
                initOptions(categorys)
            } else {
                // 
                return categorys // ==> asyncpromisevaluecategorys
            }


        }
    }

    const initOptions = async (categorys) => {
        //categorysoptions
        const options = categorys.map(item => ({
            //map(c=>({})) 
            value: item._id,
            label: item.name,
            isLeaf: false //
        }))

        if (params.id) {
            const subCategorys = await getCategorys(params.id)
            const childOption = subCategorys.map(item => ({
                value: item._id,
                label: item.name,
                isLeaf: true
            }))
            const targetOption = options.find(option => option.value === params.id)
            targetOption.children = childOption
            setSpinning(false)
        }
        setOptions(options)

    }

    //()
    const loadSubData = async (selectedOptions) => {
        //option

        const targetOption = selectedOptions[0];

        //loading
        targetOption.loading = true;
        //value _id 
        const subCategorys = await getCategorys(targetOption.value)

        if (subCategorys && subCategorys.length > 0) {
            //option
            targetOption.loading = false
            const childOption = subCategorys.map(item => ({
                value: item._id,
                label: item.name,
                isLeaf: true
            }))
            //otpionoptionchildren
            targetOption.children = childOption
        } else { //
            targetOption.loading = false
            targetOption.isLeaf = true
        }
        setOptions([...options])

    };
    //  
    const getImgUrl = (file) => {
        // console.log(file);
        if (file.status === 'done') {
            setImgUrl(file.response.data.name)
        }
    }
    //2.
    const handleCancel = () => setPreviewVisible(false); //

    //
    const handlePreview = async file => {
        if (!file.url && !file.preview) {
            file.preview = await getBase64(file.originFileObj);
        }
        setPreviewImage(file.url || file.preview)
        setPreviewTitle(file.name || file.url.substring(file.url.lastIndexOf('/') + 1))
        setPreviewVisible(true)

    };
    //change
    const handleChange = async ({ file, fileList }) => {
        //(/)fileList
        // props.getImgUrl(file)

        if (file.status === 'done') {
            setImgUrl(file.response.data.name)
            const result = file.response
            if (result.status === 0) {
                message.success('')
                const { name, url } = result.data
                file = fileList[fileList.length - 1]
                file.name = name
                file.url = url
            } else {
                message.error('')
            }
        } else if (file.status === 'removed') {
            //
            const result = await reqDelImg(file.name)
            if (result.data.status === 0) {
                message.success('')
            } else {
                message.error('')
            }
        }

        setFileList(fileList)
    }

    const uploadButton = (
        <div>
            <PlusOutlined />
            <div style={{ marginTop: 8 }}>Upload</div>
        </div>
    );
    //2.end

    //
    const onCheck = async () => {
        try {
            const values = await form.validateFields();
            console.log('Success:', values);
            const { name, desc, price, category } = values
            // console.log(values.category.length > 1);
            //api 
            if (params.id) {
                console.log(paramsId);
                //
                const result = await reqUpdateProduct(paramsId, name, imgUrl, desc, price, values.category.length > 1 ? category[1] : category[0], values.category.length > 1 ? category[0] : '0')
                if (result.data.status === 0) {
                    message.success('')
                    //
                    history.push({ pathname: '/products:/product', params: { pageNum: props.location.params.pageNum } })
                }
            } else {
                reqAddProducts(name, imgUrl, desc, price, values.category.length > 1 ? category[1] : category[0], values.category.length > 1 ? category[0] : '0')
                    .then(res => {
                        console.log(res);
                        //
                        if (res.data.status === 0) {
                            history.push('/products:/product')
                            message.success('')
                        } else {
                            message.error(res.data.msg)
                        }

                    })
                    .catch(err => message.error(''))
            }

        } catch (errorInfo) {
            console.log('Failed:', errorInfo);
        }
    };
    return (

        <Card title={title}>
            <Spin spinning={spinning}>
                <Form form={form} name="dynamic_rule">
                    <Form.Item
                        {...formItemLayout}
                        name="name"
                        label=""
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                        ]}
                    >
                        <Input placeholder="" />
                    </Form.Item>
                    <Form.Item
                        {...formItemLayout}
                        name="desc"
                        label=""
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                        ]}

                    >
                        <TextArea placeholder="" autoSize={{ minRows: 2, maxRows: 6 }} />
                    </Form.Item>
                    <Form.Item
                        {...formItemLayout}
                        name="price"
                        label=""
                        rules={[
                            {
                                required: true,
                                message: '',

                            },
                            {
                                validator: validatorPrice
                            }
                        ]}

                    >
                        <Input prefix="" suffix="RMB" placeholder="" />
                    </Form.Item>
                    <Form.Item
                        {...formItemLayout}
                        name="category"
                        label=""
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                        ]}

                    >
                        <Cascader
                            options={options} //
                            loadData={loadSubData} //


                        />
                    </Form.Item>
                    <Form.Item
                        {...formItemLayout}
                        label=""

                    >
                        {/* <PicturesWall getImgUrl={getImgUrl} defaultFileList={defaultFileList}></PicturesWall> */}
                        {/* 1. */}
                        <Upload
                            action="/manage/img/upload" //
                            name='image' //  file
                            accept='image/*' //
                            listType="picture-card" //
                            fileList={fileList} //
                            onPreview={handlePreview}
                            onChange={handleChange}
                        >
                            {fileList.length >= 4 ? null : uploadButton}
                        </Upload>
                        <Modal
                            visible={previewVisible}
                            title={previewTitle}
                            footer={null}
                            onCancel={handleCancel}
                        >
                            <img alt="example" style={{ width: '100%' }} src={previewImage} />
                        </Modal>
                    </Form.Item>

                    <Form.Item {...formTailLayout}>
                        <Button type="primary" onClick={onCheck}>
                            Check
                    </Button>
                    </Form.Item>

                </Form>
            </Spin>
        </Card>

    )
}

export default Edit
