
import React from 'react'
import { useHistory, Redirect } from 'react-router-dom'
import './login.less'
import logo from '../../assets/images/logo.png'
import { Form, Input, Button, Checkbox, message } from 'antd'
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import Password from 'antd/lib/input/Password';
import { reqLogin } from '../../api/index'
import memoryUtils from '../../utils/memoryUtils'
import {setUser,getUser,removeUser} from '../../utils/storageUtils'
/**
 * 
 */

/**
 * asyncawait
 * 1.
 *     promise:then()/
 *     ()
 * 2.await
 *     promiseawait:promise,promisevalue
 * 3.async
 *     awaic()
 */
function login(props) {
    const [form] = Form.useForm();
    // const history = useHistory();
    const user = memoryUtils.user
    if(user._id){
        return <Redirect to="/"></Redirect>
    }
    const onCheck = async () => {
        try {
            const values = await form.validateFields();
            //
            const { username, password } = values
            //
            reqLogin(username, password)
                .then(res => {
                    console.log(res);
                    if (res.data.status === 0) {
                        //
                        message.success('')
                        memoryUtils.user=res.data.data
                        console.log(res.data.data);
                        setUser(res.data.data)
                        //replace 
                        props.history.replace('/')
                    } else {
                        //
                        message.error(res.data.msg)
                    }
                })
                .catch(err => {

                })
        } catch (errorInfo) {
            console.log('Failed:', errorInfo);
        }
    };



    return (
        <div className="login">
            <header className="login-header">
                <img src={logo} alt="logo" />
                <h1>React:</h1>
            </header>
            <section className="login-content">
                <h2></h2>
                <Form
                    name="normal_login"
                    className="login-form"
                    form={form}
                    initialValues={{ remember: true }}
                >
                    <Form.Item
                        name="username"
                        rules={[{ required: true, message: '' }]}
                    >
                        <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Username" />
                    </Form.Item>
                    <Form.Item
                        name="password"
                        rules={[
                            {
                                required: true, message: ''
                            }

                        ]}
                    >
                        <Input
                            prefix={<LockOutlined className="site-form-item-icon" />}
                            type="password"
                            placeholder="Password"
                        />
                    </Form.Item>
                    <Form.Item>
                        <Form.Item name="remember" valuePropName="checked" noStyle className="login-form-checkbox">
                            <Checkbox></Checkbox>
                        </Form.Item>

                    </Form.Item>

                    <Form.Item>
                        <Button type="primary" onClick={onCheck} className="login-form-button">
                            
        </Button>

                    </Form.Item>
                </Form>
            </section>
        </div>
    )
}

export default login
