import React from 'react'
import { useHistory, withRouter } from 'react-router-dom'
import './index.less'
import { Menu } from 'antd';
import menuList from '../../config/menuConfig'
import memoryUtils from '../../utils/memoryUtils'
import logo from '../../assets/images/logo.png'

const { SubMenu } = Menu;
/**
 * 
 */
function LeftNav(props) {
    const history = useHistory();
    const path = props.location.pathname
    const hasAuth = (item) => {
        const { key, isPublic } = item
        const menus = memoryUtils.user.role.menus
        const username = memoryUtils.user.username
        if (username === 'admin' || isPublic || menus.indexOf(key) != -1) {
            return true
        }
        return false

    }

    return (
        <div className="left-nav">
            <header className="left-nav-header">
                <img src={logo} alt="" />
                <h1>React</h1>
            </header>

            <Menu
                // menu.item 
                selectedKeys={[path]}
                // 
                defaultOpenKeys={[path.slice(0, path.search(/:/) + 1)]}
                mode="inline"

                theme="dark"
            >
                {/*   */}
                {
                    menuList.map(item => {
                        if (hasAuth(item)) {
                            if (!item.children) {
                                return <Menu.Item
                                    key={item.key}
                                    icon={item.icon}
                                    onClick={() => history.push(item.key)}
                                >
                                    {item.title}
                                </Menu.Item>
                            } else {
                                return <SubMenu
                                    key={item.key}
                                    title={item.title}
                                    icon={item.icon}
                                    onTitleClick={subKey => { props.match.path = subKey.key }}
                                >
                                    {item.children.map(p => {
                                        return <Menu.Item key={p.key} icon={p.icon} onClick={() => history.push(p.key)}>{p.title}</Menu.Item>
                                    })}
                                </SubMenu>
                            }
                        }
                    })
                }
            </Menu>
        </div>
    )
}
/**
 * withRouter, Route, 
 * react-routerhistory, location, match
 * props.
 * withRouter: 
 *  Route, 
 * const withRouter = () => {
 *  return () => {
 * return <Route component={Nav} />
 *      }
 * }
 */

export default withRouter(LeftNav)
