import React, { useState, useEffect } from 'react'
import { Button, Card, Table, Modal, Form, Input, Select, Tag } from 'antd'
import {
    TwitterOutlined,

    FacebookOutlined,

} from '@ant-design/icons';
import { reqCategorys, reqAddCategorys, reqUpdataCategorys, reqDelCategorys } from '../../api/index'
import './index.less'
const { Option, OptGroup } = Select;
//from 
const layout = {
    labelCol: {
        span: 4,
    },
    wrapperCol: {
        span: 16,
    },
};

/**
 * 
 */
function Category() {

    const [categorys, setCategorys] = useState([]) //
    const [visible, setVisible] = useState(false) //
    const [modifyVisible, setModifyVisible] = useState(false) //
    const [modifyId, setModifyId] = useState('') //Id
    const [parentId, setParentId] = useState('0') //id
    const [parentName, setParentName] = useState('') //
    const [level, setLevel] = useState(0)// parentId
    const [loading, setLoading] = useState(false) //

    const [form] = Form.useForm()  //form

    useEffect(() => {
        //api
        loadData('0')
    }, [])

    // useStateprandId
    const loadData = (parentId) => {
        setLoading(true)
        reqCategorys(parentId)
            .then(res => {
                setLoading(false)
                setCategorys(res.data.data)
                console.log(res.data.data)
            })
    }


    //modal 
    const handleOk = async () => {
        try {
            const values = await form.validateFields();
            console.log('Success:', values);
            //
            if (level === 0) {
                reqAddCategorys('0', values.name)
                    .then(res => {
                        setVisible(false)
                        loadData()
                    })
                    .catch(err => console.log(err))
            } else {
                reqAddCategorys(level, values.name)
                    .then(res => {
                        // console.log(level)
                        setVisible(false)
                        loadData(parentId)
                    })
                    .catch(err => console.log(err))
            }


        } catch (errorInfo) {
            // console.log('Failed:', errorInfo);
        }
    };
    //
    const modifyHandleOk = async () => {
        try {
            const values = await form.validateFields();
            console.log('Success:', values);
            //
            // console.log(modifyId)
            // console.log(values.modifyName)
            reqUpdataCategorys(modifyId, values.modifyName)
                .then(res => {
                    setModifyVisible(false)
                    loadData('0')
                    console.log(res)
                })
                .catch(err => console.log(err))

        } catch (errorInfo) {
            console.log('Failed:', errorInfo);
        }
    }
    //
    const columns = [
        {
            title: '',
            dataIndex: 'name'

        },
        {
            title: '',
            render: (txt, record, index) => {
                return (
                    <div>
                        <Tag icon={<TwitterOutlined />} color="#55acee">
                            Twitter
                        </Tag>
                        <Tag icon={<FacebookOutlined />} color="#3b5999">
                            Facebook
                        </Tag>
                    </div>
                )
            }
        },
        {
            title: '',
            render: (txt, record, index) => {

                return (
                    <div style={{ textAlign: "center" }}>
                        <Button type="primary" onClick={() => {
                            //
                            setModifyVisible(true)
                            //useStateid
                            setModifyId(record._id)
                            //formsetFieldsValue init
                            form.setFieldsValue({
                                modifyName: record.name
                            })
                        }}></Button>
                        {parentId === '0' ? (
                            <Button style={{ margin: "0 15px" }} onClick={() => {
                                //prandid
                                // setPrandId('1')
                                //titleParentName
                                console.log(record)
                                setParentName(record.name)
                                setParentId(record._id)
                                setLevel(record._id)

                                loadData(record._id)




                            }}></Button>) : (
                                <Button style={{ margin: "0 15px" }} onClick={() => {
                                    setParentId('0')
                                    setParentName('')
                                    setLevel('0')
                                    loadData('0')
                                }}></Button>
                            )}
                        <Button
                            type="danger"
                            onClick={() => {
                                reqDelCategorys(record.name, record._id)
                                    .then(res => {
                                        //
                                        loadData(level)
                                    })
                            }}></Button>
                    </div>
                )
            }
        }
    ]
    return (
        <div className="category">

            <Card
                title={parentName ? ` ->  ${parentName}` : ''}
                extra={
                    <div className="Card-extra">

                        <Button type="primary" onClick={() => setVisible(true)}
                        >+ </Button>
                    </div>

                } >
                <Table
                    rowKey="_id"
                    loading={loading}
                    tableLayout='fixed'
                    dataSource={categorys}
                    columns={columns}
                    bordered
                ></Table>
            </Card>
            {/*  */}
            <Modal
                title=""
                visible={visible}
                destroyOnClose={() => { return true }}
                onOk={handleOk}
                onCancel={() => setVisible(false)}
                style={{ textAlign: "center" }}

            >
                <Form
                    {...layout}
                    name="basic"
                    form={form}

                >
                    <Form.Item label="">
                        <Select defaultValue={parentId ? parentName : '0'} style={{ textAlign: "center" }} onChange={(p) => setLevel(p)}>
                            <OptGroup label="">
                                <Option value="0"></Option>
                                {categorys.map(item => {
                                    return (
                                        <Option value={item._id}>{item.name}</Option>
                                    )
                                })}
                            </OptGroup>

                        </Select>
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="name"
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                        ]}
                    >
                        <Input />
                    </Form.Item>
                </Form>
            </Modal>
            <Modal
                title=""
                visible={modifyVisible}
                destroyOnClose={() => { return true }}
                onOk={modifyHandleOk}
                onCancel={() => setModifyVisible(false)}
                style={{ textAlign: "center" }}
            >
                <Form
                    {...layout}
                    name="basic"
                    form={form}
                >
                    <Form.Item
                        label=""
                        name="modifyName"
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                        ]}
                    >
                        <Input />
                    </Form.Item>

                </Form>
            </Modal>
        </div>
    )
}

export default Category
