import React from 'react'
import {
    AppstoreOutlined,
    DeploymentUnitOutlined,
    BarsOutlined,
    AppstoreAddOutlined,
    SolutionOutlined,
    UserOutlined,
    AreaChartOutlined,
    PieChartOutlined,
    LineChartOutlined,
    BarChartOutlined
} from '@ant-design/icons';
const menuList = [
    {
        title: '', //  
        key: '/home', //  path 
        icon: <DeploymentUnitOutlined spin />, //  
        isPublic:true
    }, {
        title: '',
        key: '/products:',
        icon: <AppstoreOutlined />,
        children: [ //  
            {
                title: '',
                key: '/products:/category',
                icon: <BarsOutlined />
            }, {
                title: '',
                key: '/products:/product',
                icon: <AppstoreAddOutlined />
            },
        ]
    }, {
        title: '',
        key: '/user',
        icon: <UserOutlined />
    }, {
        title: '',
        key: '/role',
        icon: <SolutionOutlined />
    }, {
        title: '',
        key: '/charts:',
        icon: <AreaChartOutlined />,
        children: [
            {
                title: '',
                key: '/charts:/bar',
                icon: <BarChartOutlined />
            }, {
                title: '',
                key: '/charts:/line',
                icon: <LineChartOutlined />
            }, {
                title: '',
                key: '/charts:/pie',
                icon: <PieChartOutlined />
            },]
    }
]


export default menuList