/**
 * 
 */
import React, { useState, useEffect } from 'react'
import { Button, Card, Table, Modal, Form, Input, Select, message } from 'antd'
import { reqAddUser, reqDelUser, reqUser,reqUpdateUser } from '../../api/index'
import {formateDate} from '../../utils/dateUtils'
const { Option } = Select;
function User() {
    const layout = {
        labelCol: {
            span: 4,
        },
        wrapperCol: {
            span: 16,
        },
    };
    const columns = [
        {
            title: '',
            dataIndex: 'username'
        },
        {
            title: '',
            dataIndex: 'email'
        },
        {
            title: '',
            dataIndex: 'phone'
        },
        {
            title: '',
            dataIndex: 'create_time',
            render:(create_time)=>formateDate(create_time)
        },
        {
            title: '',
            render: (txt, record, index) => {
                return (<div style={{ float: "left", marginLeft: "10%", width: "80%" }}>
                    <Button
                        style={{
                            float: "left",
                            marginLeft: "2.5%",
                            width: "30%"
                        }}
                        onClick={()=>modifyUser(record)}
                    >
                    </Button>
                    <Button
                        type="danger"
                        style={{
                            float: "left",
                            marginLeft: "2.5%",
                            width: "30%"
                        }}
                        onClick={()=>{delUser(record._id)}}
                    >
                        
                     </Button>
                </div>
                )
            }
        }
    ]
   
    const selectAfter = (
        <Select defaultValue="@qq.com" className="select-after">
            <Option value="@qq.com">@qq.com</Option>
            <Option value="@163.com">@163.com</Option>

        </Select>
    );
    const [isShow, setIsShow] = useState(false); //modal
    const [roles, setRoles] = useState([]) //
    const [users, setUsers] = useState([]) //
    const [title, setTitle] = useState([]) //title
    const [updataUserId,setUpdataUserId] = useState("") //_id
    const [form] = Form.useForm()
    useEffect(() => {
        //api
        loadUser()
    }, [])

    const loadUser = () => {
        reqUser()
            .then(res => {
                console.log(res);
                if (res.data.status === 0) {
                    setUsers(res.data.data.users)
                    setRoles(res.data.data.roles)
                }
            })
            .catch(err => {
                console.log(err);
            })
    }
    const delUser = (id)=>{
        reqDelUser(id)
        .then(res=>{
            if(res.data.status===0){
                message.success("")
                //
                loadUser()
            }else{
                message.error("!")
                loadUser()
            }
        })
    }
    const addUserClick = () => {
        //btn
        setTitle("")
        setIsShow(true)
       

    }
    const modifyUser = (record)=>{
        //btn
        setUpdataUserId(record._id)
        setIsShow(true)
        setTitle("")
        form.setFieldsValue({
            username:record.username,
            email:record.email,
            phone:record.phone,
            create_time:record.create_time,
            role_id:record.role_id
            
        })
        console.log(record); 
    }
    const handleOk = async () => {
        //modalOK
        //
        try {
            const values = await form.validateFields();
            console.log('Success:', values);
            //api 
            if(title===""){
                const result = await reqAddUser(values)
                if (result.data.status === 0) {
                    message.success("")
                    loadUser()
                } else {
                    message.error("")
                    loadUser()
                }
               
            }else{
                const result = await reqUpdateUser({...values,_id:updataUserId})
                console.log(result);
                if (result.data.status === 0) {
                    message.success("")
                    loadUser()
                } else {
                    message.error("")
                    loadUser()
                }
            }
            setIsShow(false)
        } catch {

        }
    }
    const handleCancel = () => {
        //modalCancel
        setIsShow(false)
    }
    const cradExtra = (<Button type="primary" onClick={addUserClick}></Button>)

    return (
        <Card extra={cradExtra}>
            <Table
                rowKey="_id"
                bordered
                columns={columns}
                dataSource={users}
                rowKey={record => record._id}
            >

            </Table>
            <Modal
                title={title}
                visible={isShow}
                onOk={handleOk}
                onCancel={handleCancel}
            >
                <Form
                    {...layout}
                    name="basic"
                    form={form}
                // onFinish={onFinish}
                // onFinishFailed={onFinishFailed}
                >
                    <Form.Item
                        label=""
                        name="username"
                        rules={[{ required: true, message: '!' }]}
                    >
                        <Input placeholder="" />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="password"
                        rules={[{ required: true, message: '!' }]}
                    >
                        <Input type="password" placeholder="" />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="phone"

                    >
                        <Input placeholder="" />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="email"

                    >
                        <Input
                            placeholder=""
                            addonAfter={selectAfter}
                        />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="role_id"

                    >
                        <Select>
                            {roles.map(item => {
                                //Option 
                                return <Option value={item._id}>{item.name}</Option>
                            })}
                        </Select>
                    </Form.Item>

                </Form>
            </Modal>
        </Card>
    )
}

export default User
